import React, { LegacyRef, MutableRefObject, ReactChild } from 'react';
import Box from '@mui/material/Box';
import {
	Button,
	Container,
	Divider,
	Grid,
	Stack,
	TextField,
	Typography,
} from '@mui/material';
import TextForm from './TextForm';
import { Link } from 'react-router-dom';
import { FONT_COLORS, SCREEN_NAMES } from './const';
import BgImageSelectTabs, { P_BgImageSelectTabs } from './BgImageSelectTabs';
import FontFamilySelector from './FontFamilySelector';
import FontSizeSelector from './FontSizeSelector';
import ColorPicker, { P_ColorPicker } from './ColorPicker';
import FontStrokeColorSelector from './FontStrokeColorSelector';
import NumberInputForm, { P_NumberInputFormBase } from './NumberInputForm';
import SelectorForEdit, { P_SelectorForEditBase } from './SelectorForEdit';
import Canvas, { P_Canvas, t_bgImageMode } from './Canvas';
import { downloadURI } from './UtilsDom';
import RadioButtonsForEdit, {
	P_RadioButtonsForEditBase,
} from './RadioButtonsForEdit';
import { t_loading } from './Product';
import LoadingBgImage from './LoadingBgImage';

export default function ScreenEdit(props: {
	bgImageLoading: t_loading;
	propsBgImageSelectTabs: P_BgImageSelectTabs;
	propsBgImageModeRadioButton: P_RadioButtonsForEditBase;
	propsFontFamilySelector: P_SelectorForEditBase<string>;
	propsFontSizeSelector: P_SelectorForEditBase<number>;
	propsColorPicker: P_ColorPicker;
	propsFontStrokeColorRadioButton: P_RadioButtonsForEditBase;
	propsLineHeightForm: P_NumberInputFormBase;
	propsStageWidth: P_NumberInputFormBase;
	propsCanvas: P_Canvas;
}) {
	const outputFontFamilyItem = React.useCallback(
		(e: string) => e.split(',')[0],
		[],
	);

	console.log('ScreenEdit rendered');
	return (
		<Container maxWidth={'lg'}>
			<Grid
				container
				spacing={3}
				direction='row-reverse'
				sx={{ display: 'flex', justifyContent: 'center' }}
			>
				<Grid item md={4} sm={12} width={'100%'} zeroMinWidth>
					<Stack spacing={3}>
						<Save
							selectedModeIndex={
								props.propsBgImageModeRadioButton.selectedItemIndex
							}
							changeMode={props.propsBgImageModeRadioButton.onChange}
							stageRef={props.propsCanvas.ref_stage}
						/>

						<Typography variant='h6'></Typography>

						<BgImageSelectTabs {...props.propsBgImageSelectTabs} />

						{/*<Divider /> */}

						{props.bgImageLoading !== 'SUCCESS' ? (
							<LoadingBgImage loading={props.bgImageLoading} />
						) : (
							<RadioButtonsForEdit
								{...props.propsBgImageModeRadioButton}
								id={'bg-image-mode-radiobuttons'}
							/>
						)}

						<Typography variant='h6'></Typography>

						<SelectorForEdit
							{...props.propsFontFamilySelector}
							id={'font-family-selector'}
							label=''
							valueOutput={outputFontFamilyItem}
						/>

						<SelectorForEdit
							{...props.propsFontSizeSelector}
							id='font-size'
							label=''
						/>

						<div>
							<Typography
								variant='subtitle2'
								color={'textSecondary'}
								gutterBottom
							>
								
							</Typography>

							<ColorPicker
								colorList={props.propsColorPicker.colorList}
								selectedColor={props.propsColorPicker.selectedColor}
								onChange={props.propsColorPicker.onChange}
							/>
						</div>

						<NumberInputForm
							id={'line-height'}
							label=''
							shrink={true}
							value={props.propsLineHeightForm.value}
							onChange={props.propsLineHeightForm.onChange}
							inputStep={0.1}
						/>

						<RadioButtonsForEdit
							{...props.propsFontStrokeColorRadioButton}
							id={'font-stroke-color'}
							label=''
							row={true}
						/>

						<Typography variant='h6'></Typography>

						<NumberInputForm
							id={'stage-width'}
							label=''
							shrink={true}
							value={props.propsStageWidth.value}
							onChange={props.propsStageWidth.onChange}
							inputStep={1}
						/>
					</Stack>
				</Grid>
				<Grid
					item
					md={8}
					sm={12}
					zeroMinWidth
					sx={{ display: 'flex', justifyContent: 'center' }}
				>
					<ScrollableContainer>
						<Canvas {...props.propsCanvas} />
					</ScrollableContainer>
				</Grid>
			</Grid>
			<div style={{ textAlign: 'center' }}>
				<span>iiiiiiiiiiiiiii</span>
			</div>
		</Container>
	);
}

const Save = (props: {
	selectedModeIndex: number;
	changeMode: (n: number) => any;
	stageRef: P_Canvas['ref_stage'];
}) => {
	if (props.selectedModeIndex === 2) {
		return (
			<Button
				variant='contained'
				onClick={() => {
					props.changeMode(3);
				}}
			>
				
			</Button>
		);
	}
	const save = () => {
		const uri = props.stageRef.current?.toDataURL();
		uri && downloadURI(uri, 'canvas.png');
	};

	return (
		<Button variant='contained' onClick={save}>
			
		</Button>
	);
};

const ScrollableContainer = (props: { children: React.ReactElement }) => {
	return (
		<div
			style={{
				overflow: 'auto',
				maxWidth: '100%',
				//maxHeight: '90vh',
			}}
		>
			{props.children}
		</div>
	);
};
